import React from 'react'
import BlurText from '@/components/BlurText'


export default function Work() {
  return (
    <div className="bg-black text-white flex flex-col items-center min-h-screen">

<BlurText
  text="WORK"
  delay={300}
  animateBy="words"
  direction="top"
  className="text-2xl mb-8"
/>
    </div>
  )
}
